import React from "react";
import { Grid, makeStyles, Switch } from "@material-ui/core";
import { TAssetSummaryInMarket, TMarketType } from "./types";
import clsx from "clsx";
import { useCommonClasses } from "../../../theme/commonClasses";

interface IProps {
  assetSummary: TAssetSummaryInMarket;
  marketType: TMarketType;
}

const useStyles = makeStyles((theme) => ({
  // TODO : Consider making this global
  a: {
    color: "inherit",
    letterSpacing: "1px",
    fontSize: "12px",
    fontWeight: 700,
    textRransform: "uppercase",
    textDecoration: "none",
    cursor: "pointer",
  },
  asset: {
    // display: "flex",
    // alignItems: "center",
    // borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
    // padding: "1.25rem 1.75rem",
    // fontSize: "1rem",
    // fontWeight: 500,
    // textTransform: "capitalize",
    // letterSpacing: 0,
    // transition: "none",
    display: "flex",
    alignItems: "center",
    flexWrap: "wrap",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
    // padding: "1.25rem 1.75rem",
    padding: "1.25rem 1.75rem",
    borderLeft: "2px solid transparent",
    paddingLeft: "1.625rem",
    textTransform: "none",
    fontSize: "1rem",
    fontWeight: 500,
    letterSpacing: 0,
    transition: "none",

    height: "6rem",
  },

  list_icon: {
    width: "2.25rem",
    height: "2.25rem",
  },

  identity: {
    alignItems: "center",
  },

  identity_icon: {
    marginRight: "1rem",
  },

  identity_balance: {
    flex: "1 1",
    marginRight: "0.25rem",
    overflow: "hidden",
  },

  balance: {
    "& > div": {
      whiteSpace: "nowrap",
      overflow: "hidden",
      textOverflow: "ellipsis",
    },
  },

  balanceSubtitle: {
    display: "block",
    marginTop: "9px",
    color: "#AAB8C1",
    fontSize: "0.9em",
  },
}));

const PUBLIC_URL = process.env.PUBLIC_URL;

export const AssetPanelRow = React.memo<IProps>((props) => {
  const classes = useStyles();
  const commonClasses = useCommonClasses();
  const { marketType, assetSummary } = props;

  const {
    name,
    symbol,
    amountInWallet,
    borrowAPy,
    isCollateralable,
    supplyApy,
    usdLiquidity,
  } = assetSummary;

  const liquidityInMillionUSD = usdLiquidity / 1_000_000;

  const ApyToDisplay = marketType === "supply" ? supplyApy : borrowAPy;

  return (
    <a className={clsx([classes.a, classes.asset])}>
      {/* Asset (+APY subtitle) */}
      <Grid container item xs={4} sm={4} className={classes.identity}>
        <span
          onClick={() => alert("Should open asset modal")}
          className={clsx([
            commonClasses.icon,
            classes.identity_icon,
            classes.list_icon,
          ])}
          style={{
            backgroundImage: `url(${PUBLIC_URL}/tokens/asset_${symbol}.svg)`,
          }}
        />
        <div className={clsx([classes.balance, classes.identity_balance])}>
          <div>{name}</div>
          <span
            className={clsx([
              commonClasses.mobileOnly,
              classes.balanceSubtitle,
            ])}
          >
            {ApyToDisplay}%
          </span>
        </div>
      </Grid>

      {/* APY (mobile hidden) */}
      <Grid
        item
        xs={1}
        sm={3}
        className={clsx([commonClasses.mobileHide, commonClasses.textRight])}
      >
        <div className={clsx([classes.balance])}>
          <div>{ApyToDisplay}%</div>
        </div>
      </Grid>

      {/* Wallet */}
      <Grid item xs={4} sm={3} className={clsx([commonClasses.textRight])}>
        <div className={clsx([classes.balance])}>
          <div>
            {amountInWallet} {symbol}
          </div>
        </div>
      </Grid>

      {/* Collateral / Liquidity */}
      <Grid item xs={4} sm={2} className={clsx([commonClasses.textRight])}>
        <div className={clsx([classes.balance])}>
          {/* TODO : Add a proper toggle component */}
          {marketType === "supply" ? (
            // <input type="checkbox" />
            <Switch checked={isCollateralable} />
          ) : (
            <div>${liquidityInMillionUSD.toFixed(2)}M</div>
          )}
        </div>
      </Grid>
    </a>
  );
});
