function buildAssetSummary(
  symbol: string,
  name: string,
  supplyApy: number,
  borrowAPy: number,
  isCollateralable: boolean,
  amountInWallet: number,
  usdLiquidity: number
): TAssetSummaryInMarket {
  const assetSummaryInMarket: TAssetSummaryInMarket = {
    symbol,
    name,
    supplyApy,
    borrowAPy,
    isCollateralable,
    amountInWallet,
    usdLiquidity,
  };

  return assetSummaryInMarket;
}

