import React from "react";
import { Panel } from "../../../components/panel/Panel";
import { Grid, makeStyles } from "@material-ui/core";
import { useCommonClasses } from "../../../theme/commonClasses";
import clsx from "clsx";
import { AssetPanelRow } from "./AssetPanelRow";
import { TAssetSummaryInMarket, TMarketType } from "./types";

interface IProps {
  header: string;
  marketType: TMarketType;
  assetsInMarket: TAssetSummaryInMarket[];
}

const useStyles = makeStyles((theme) => ({
  panelHeader: {
    display: "flex",
    flexFlow: "inherit",
    alignItems: "center",
    justifyContent: "space-between",
    fontSize: "1.1rem",
    padding: "1rem 1.75rem",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
  },
  zeroMargin: {
    margin: 0,
  },
  assetsList: {},
  panelLabels: {
    // display: "flex",
    padding: "1rem 1.75rem",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
    alignItems: "center",
  },
  assets: {},
}));

export const MarketsPanel = React.memo<IProps>((props) => {
  const classes = useStyles();
  const commonClasses = useCommonClasses();
  const { header, marketType, assetsInMarket } = props;

  return (
    <Panel>
      <div className={classes.panelHeader}>
        <h4 className={classes.zeroMargin}>{header}</h4>
      </div>
      <div className={classes.assetsList}>
        <Grid container direction={"row"} className={classes.panelLabels}>
          {/* Asset */}
          <Grid item xs={4} sm={4}>
            <label className={commonClasses.label}>Asset</label>
          </Grid>

          {/* APY */}
          <Grid className={commonClasses.textRight} item xs={1} sm={3}>
            <label
              className={clsx([commonClasses.label, commonClasses.mobileHide])}
            >
              APY
            </label>
          </Grid>

          {/* WALLET */}
          <Grid className={commonClasses.textRight} item xs={4} sm={3}>
            <label
              className={clsx([
                commonClasses.label,
                commonClasses.textRight,
                commonClasses.mobileHide,
              ])}
            >
              Wallet
            </label>
          </Grid>

          {/* Collateral/Liquidity */}
          <Grid className={commonClasses.textRight} item xs={4} sm={2}>
            <label
              className={clsx([
                commonClasses.label,
                commonClasses.textRight,
                commonClasses.mobileHide,
              ])}
            >
              {marketType === "supply" ? "Collateral" : "Liquidity"}
            </label>
          </Grid>
        </Grid>

        <div className={classes.assets}>
          {assetsInMarket.map((asset) => (
            <AssetPanelRow
              marketType={marketType}
              assetSummary={asset}
              key={asset.symbol}
            />
          ))}
        </div>
      </div>
    </Panel>
  );
});
